.Button {
  background-color: rgba(255, 255, 255, 0.7);
  font-size: 12px;
  padding: 3px 6px;
  color: #444;
  border: 1px solid;
  cursor: pointer;
  display: inline-block;
  text-transform: uppercase;
}

.Button:hover {
  background-color: rgba(255, 255, 255, 0.9);
  color: #222;
}



